import { Tag } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../../../models/baseModel";
import { WorkflowService } from "../../../../../services/taskService";
import { MyBox } from "../../../../core/MyBox";
import { MyButton } from "../../../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../../../core/TableModel";
import TableComponent from "../../../../core/TableNewComponent";
import { Text } from "../../../../core/Text";
import { UIFormatDateTimeVNComponent } from "../../../../core/UI_FormatComponent";
import ModifyComponent from "./ModifyComponent";

export default function IndexComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [visible, setVisible] = useState(false);

    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>();
    const [projectKey, setProjectKey] = useState<string | undefined>();

    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const column: any = [
        new TableColumn({ title: "STT", key: "stt", render: (data: any, row: any, index: number) => Fn.RenderSTT(data, row, index, pageInfo) }),
        new TableColumn({ title: "Tên", key: "name", render: (data: string, row: any) => <MyButton onClick={() => { handleClickDetail(row.key, row.project?.key) }} type="link" value={data} /> }),
        new TableColumn({ title: "Dự án", key: "project", render: (data: any) => <Text>{data.name}</Text> }),
        new TableColumn({
            title: "Trạng thái", key: "status", render: (data: number) => {
                let statusStr: string | undefined;
                let color: string | undefined;
                switch (data) {
                    case 0:
                        statusStr = "Tạo mới";
                        color = "blue";
                        break;
                    case 1:
                        statusStr = "Đang chạy";
                        color = "gray";
                        break;
                    case 2:
                        statusStr = "Thành công";
                        color = "green";
                        break;
                    case -1:
                        statusStr = "Lỗi";
                        color = "purple";
                        break;
                    case -2:
                        statusStr = "Hủy";
                        color = "red";
                        break;
                    default:
                        break;
                }
                return <Tag color={color}>{statusStr}</Tag>
            }
        }),
        new TableColumn({ title: "Ngày tạo", key: "createdAt", render: (data: string) => <UIFormatDateTimeVNComponent value={data} /> }),
        new TableColumn({ title: "Ngày cập nhật cuối", key: "updatedAt", render: (data: string) => <UIFormatDateTimeVNComponent value={data} /> }),
    ];

    const loadData = useCallback(() => {
        setIsLoading(true);
        let paginationRequest: PaginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        WorkflowService.GetList(paginationRequest)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setPageInfo(new PageInfoModel(rs));
                    setData(rs.data.pagedData);
                }
            })
            .finally(() => setIsLoading(false));
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleClickAdd() {
        setVisible(true);
    }

    function handleClickDetail(key: string | undefined, projectKey: string | undefined) {
        setKeyEdit(key);
        setProjectKey(projectKey);
        setVisibleEdit(true);
    }

    return (
        <MyBox title="Quản lý Workflow">
            <MyButton
                type="primary"
                value="Thêm mới"
                isLoading={isLoading}
                onClick={handleClickAdd} />

            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                loading={isLoading}
                loadData={loadData}
                column={column}
                dataSource={data} />

            <ModifyComponent
                visible={visible}
                setVisible={setVisible}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />

            <ModifyComponent
                keyEdit={keyEdit}
                setKeyEdit={setKeyEdit}
                projectKey={projectKey}
                setProjectKey={setProjectKey}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />
        </MyBox>
    );
}