export function Text(props: any) {
    const { children } = props;
    return <span>{children}</span>
}

export function TextStrong(props: any) {
    const { value } = props;
    return <strong>{value}</strong>
}

export function TextWrap(props: any) {
    const { children } = props;
    return <span style={{"wordBreak": "break-all"}}>{children}</span>
}