import { Col, Drawer, Form, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import { Fn } from "../../../../../helper/function";
import { TaskProjectManageService, WorkflowService } from "../../../../../services/taskService";
import { MyFormItem } from "../../../../core/Input";
import { MyButton } from "../../../../core/MyButton";
import SearchComponent from "../../../../core/SearchComponent";

export default function ModifyComponent(props: any) {
    const { visible, setVisible, keyEdit, setKeyEdit, isLoading, setIsLoading, loadData, projectKey, setProjectKey } = props;
    const [name, setName] = useState();

    //search
    const [dataSearch, setDataSearch] = useState([]);
    const [resultKey, setResultKey] = useState('');
    const [resultName, setResultName] = useState('');


    const loadDataDetail = useCallback(() => {
        if (keyEdit !== undefined) {
            WorkflowService.GetDetail(projectKey, keyEdit)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setName(rs.data.name);
                        setResultKey(rs.data.project.key);
                        setResultName(rs.data.project.name);
                    }
                })
        }
    }, [keyEdit, projectKey]);

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail])

    function onClose() {
        setVisible(false);
        if (setKeyEdit !== undefined) {
            setKeyEdit(undefined);
        }

        if (setProjectKey !== undefined) {
            setProjectKey(undefined);
        }
    }

    function handleSubmitForm() {
        setIsLoading(true);
        var body = {
            name: name
        }

        if (keyEdit !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function edit(body: any) {
        WorkflowService.GetDetail(resultKey, keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestamp(rs, body);
                    WorkflowService.Edit(resultKey, keyEdit, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccess(rs);
                                loadData();
                            }
                            else setIsLoading(false);
                        }).finally(() => setIsLoading(false));
                } else setIsLoading(false);
            })
    }

    function add(body: any) {
        WorkflowService.Add(resultKey, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccess(rs);
                    loadData();
                } else setIsLoading(false)
            })
            .finally(() => setIsLoading(false));
    }

    function handleSearchProject(val: any) {
        setIsLoading(true);
        TaskProjectManageService.Search(val)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setDataSearch(rs.data);
                }
            })
            .finally(() => setIsLoading(false))
    }

    function handleClickDelete() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa workflow này không?"
        })
            .then((rs) => {
                if (rs.isConfirmed) {
                    DoDelete();
                }
            })
    }

    function DoDelete() {
        setIsLoading(true);
        WorkflowService.GetDetail(resultKey, keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    WorkflowService.Delete(resultKey, keyEdit, Fn.GetTimestamp(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccess(rs);
                                loadData();
                                onClose();
                            }
                            else setIsLoading(false);
                        }).finally(() => setIsLoading(false));
                } else setIsLoading(false);
            })
    }

    return (
        <Drawer
            open={visible}
            onClose={onClose}
            title={keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"}>
            <Form
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />

                <SearchComponent
                    readonly={keyEdit !== undefined}
                    label="Dự án"
                    dataSearch={dataSearch}
                    onSearch={handleSearchProject}
                    muityChoose={false}
                    resultKey={resultKey}
                    resultName={resultName}
                    setResultKey={setResultKey}
                    setResultName={setResultName}
                    isLoading={isLoading} />

                <MyButton
                    isLoading={isLoading}
                    type="success"
                    htmlType="submit"
                    value={keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"} />

                <MyButton
                    type="danger"
                    isLoading={isLoading}
                    onClick={handleClickDelete}
                    value="Xóa" />

                <Row>
                    <Col span={24}>
                        <Link to={`/tools/tasks/projects/${resultKey}/workflows/${keyEdit}/details`}>Chi tiết workflow</Link>
                    </Col>
                    <Col span={24}>
                        <Link to={`/tools/tasks/projects/${resultKey}/workflows/${keyEdit}/run`}>Workflow run</Link>
                    </Col>
                </Row>
            </Form>
        </Drawer>
    );
}