import { MethodHttp, MyHttp } from "../helper/myaxios";
import { PaginationRequest } from "../models/baseModel";

class TaskProjectManageService {
    static GetQrCode(key: string) {
        return `${MyHttp.BaseURL}/user/qrCode?data=taskManage_projectManage_${key}`;
    }

    static GetStatistic() {
        var url = `v2/tools/taskManages/projects/statistic`;
        return MyHttp.SendReq(url, "Get");
    }

    static GetLog(projectKey: string | undefined, paginationRequest: PaginationRequest) {
        var url = `v2/tools/taskManages/projects/${projectKey}/logs?${paginationRequest.generateQueryString()}`;
        return MyHttp.SendReq(url, "Get");
    }

    static GetClone(projectKey: string | undefined) {
        var url = `v2/tools/taskManages/projects/${projectKey}/duplicate`;
        return MyHttp.SendReq(url, "Get");
    }

    static ProcessClone(projectKey: string | undefined, body: any) {
        var url = `v2/tools/taskManages/projects/${projectKey}/duplicate`;
        return MyHttp.SendReq(url, "Post", body);
    }

    static GetList(paginationRequest: PaginationRequest, showClosedProject: boolean) {
        var url = `v2/tools/taskManages/projects?${paginationRequest.generateQueryString()}&showIsClosed=${showClosedProject}`;
        return MyHttp.SendReq(url, "GET");
    }

    static GetDetail(key: string | undefined) {
        var url = `v2/tools/taskManages/projects/${key}`;
        return MyHttp.SendReq(url, "Get");
    }

    static Search(q: string) {
        var url = `v2/tools/taskManages/projects/query?query=${q}`;
        return MyHttp.SendReq(url, "Get");
    }

    static Add(body: any) {
        var url = `v2/tools/taskManages/projects`;
        return MyHttp.SendReq(url, "Post", body);
    }

    static Edit(key: string | undefined, body: any) {
        var url = `v2/tools/taskManages/projects/${key}`;
        return MyHttp.SendReq(url, "Put", body);
    }

    static Delete(key: string | undefined, timestamp: string) {
        var url = `v2/tools/taskManages/projects/${key}?timestamp=${encodeURIComponent(timestamp)}`;
        return MyHttp.SendReq(url, "Delete");
    }
}

